        "vitality-upper": "LEBENSKRAFT",
        "vitality-description": " Frdert die Vitalitt.  Zur erfolgreichen Ausscheidung von Stoffwechsel-Abfallprodukten.  Verringert die tgliche Last an geistigem und krperlichem Stress. Zusatzprogramme:  Energie-Ausgleich, Energie-Spender, Nebennieren, Schilddrsen, Mineral-Stoffwechsel, Bindegewebe, Knochen, Darmflora, E smog, Alle Meridian-Programme, 1.  2. und 3. Chakra",
