        "3-chakra-description": "Befindet sich direkt unter den Rippen Und ist mit dem Verdauungssystem und den Nebennieren verbunden Verantwortlich fr persnliche Strke, Furcht, Beklmmung und Introvertiertheit Das 3.Chakra steuert die Verdauung (krperlich), persnliche Strke (geistig), Ausdehnung (emotional) und Wachstum (spirituell) Zusatzprogramme: Elemente, Meridiane und Meditation",
